.comments {
  p {padding: 0 15px;}
  .call-to-action__nav-item--back {
    text-decoration: underline;;
  }
}
.comment__form {
  input[type="submit"] {
    text-decoration: none;
    padding: 10px 0;
    margin: 5px 0;
    font-weight: normal;
    text-align: center;
    background: var(--very-dark-grayish-blue);
    color: #fff;
  }
  input[name~="submit_anonymously"] {
    background: transparent;
    color: var(--very-dark-grayish-blue);
    display: inline-block;
    text-align: right;
    padding: 0;
  }
}
.comment__date {
  display: inline;
}
.comment__post-message .icon {
  display: inline-block;
}
.article-list__comments-counter {
  .icon {
    display: inline-block;
    background-size: 78%;
    vertical-align: middle;
  }
}
.comment-counter__text {
  display: none;
  vertical-align: middle;
}
.comment-counter__number {
  padding-left: 5px;
  vertical-align: middle;
}
.comment__counter {
  font-weight: bold;
}
.comment__text {
  display: none;
}
.comment-list {
  .comment-list__item p {
    padding: 0 50px 0 0;
  }
  .pagination {
    text-align: right;
    .pagination__item {
      text-transform: uppercase;
      text-decoration: none;
      &:after {
        padding: 0 0 0 5px;
        @media only screen and (min-width: 320px) {
          content: ">";
        }
        @media only screen and (min-width: 490px) {
          content: "❯";
        }
      }
    }
  }
}
#comments-list,
#comment__list {
  .call-to-action__nav-item--response {
    position: absolute;
    right: 15px;
    top: -5px;
  }
}
#comment__list {
  p {
    padding: 0 50px 0 0;
  }
}
